body{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
main{
    background-color: #f0eaea;
    width: 70%;
    height:auto;
    margin: 20px auto;
    padding: 5px;
    text-align: center;
}
.underline{
    width:20%;
    height:3px;
    margin: -10px auto 5px auto;
    background-color: rgb(8, 153, 215);
}
.dog_card{
    width: 70%;
    height: auto;
    margin: 20px auto;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 5px 10px rgb(175, 169, 169);
    border-radius: 5px;
}
img{
    width: 100%;
    height:150px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}
p{
    text-align:justify;
    padding: 15px;
    box-sizing: border-box;
}
button{
    border: 1px solid rgb(141, 39, 39);
    padding: 5px 10px 5px 10px;
    color: rgb(179, 37, 37);
    font-weight: bolder;
    margin-bottom: 10px;
}
button:hover{
    border-color: red;
    color: red;
}
button:active{
    border-color: pink;
    color: salmon
}